<template>
  <div id="app">
    <!-- <nav>
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </nav>
    <router-view/> -->
    <RouterView ></RouterView>
    <HeaderBtn></HeaderBtn>
    <MapView :m="giveData"></MapView>
  </div>
</template>
<script>
import MapView from './components/Map.vue'
import HeaderBtn from './components/HeaderBtn.vue'
import pcOrMobile from '../src/data/pcOrMobile'
import { RouterView } from 'vue-router';

export default {
  mixins:[pcOrMobile],
  data () {
    return {
      giveData:{  //vue2.0
        height:0,
        longitude:112.20518,
        latitude:31.022003
      },
    }
  },
  components:{
   
    MapView,
    HeaderBtn, 
    RouterView,
  },
  created() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
    this.isPcOrMobile();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    handleResize() {
      this.giveData.height = window.innerHeight;
    },
    isPcOrMobile() {
      var title = ''
      var message = ''
      //-iOS-pc
      if (this.isMobile_pc()) {
        if (
          this.is_andriod_ios().iPhone ||
          this.is_andriod_ios().iPad ||
          this.is_andriod_ios().ios
        ) {
          // ios
          console.log( window.webkit,'ios');
          //  showPopupIos 
          window.webkit.messageHandlers.showPopupIos.postMessage({
            name: title,
            content: message
          });
        }
        if (this.is_andriod_ios().android) {
          // 
          // alert('android');
          // showPopupAndroid 
          AndroidJs.showPopupAndroid(title, message)
        }
      } else {
        // pc
        // console.log('pc');
        // alert('pc')
        localStorage.setItem('myDeviceType', 'pc');
      }
      // -iOS-pc
      },
  },
}
</script>
